body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  height: 100%;
  background-image: linear-gradient(to bottom, #1cb9f2, #1cf2eb);
}
.search-box {
    padding-top: 100px;
}
.weather-card {
    color: #fff;
    overflow: hidden;
    font-family: calibri, sans-serif;
    padding: 18px;
    border: 1px solid #ccc;
    border-radius: 5px;
    z-index: 400;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
}

.day-name {
    font-size: 32px;
    font-weight: bold;
    line-height: 40px;
}

.weather-icon {
    font-size: 80px;
    text-align: center;
    margin-top: 15px;
    border-bottom: 2px solid #fff;
    padding-bottom: 10px;
}

.weather-day-temp {
    font-size: 75px;
    display: inline;
    vertical-align: baseline;
    text-align: left;
    padding-right: 0.3em;
}

.weather-day-temp .units {
    letter-spacing: -0.1em;
}

.weather-details {
    display: inline-block;
    text-align: right;
    font-size: 25px;
}

.weather-details i {
    padding-right: 0.1em;
}

.weather-humidity {
    display: block;
}

.weather-windspeed {
    display: block;
}

.weather-windspeed .units{
    font-size: 15px;
    letter-spacing: -0.25em;
}

.sunny {
    background-image: radial-gradient(circle farthest-side,#ffdd40,#ffee50);
}

.cloudy {
    background-image: radial-gradient(circle farthest-side,#ccc,#ddd);
}

.rain {
    background-image: radial-gradient(circle farthest-side,#778198,#90aeaa);
}

.lightning {
    background-image: radial-gradient(circle farthest-side, #221d54,#1e3e76);
}

.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
}
.loader {
  color: #84a8fd;
  font-size: 11px;
  text-indent: -99999em;
  margin: 55px auto;
  position: relative;
  width: 10em;
  height: 10em;
  box-shadow: inset 0 0 0 1em;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
.loader:before,
.loader:after {
  position: absolute;
  content: '';
}
.loader:before {
  width: 5.2em;
  height: 10.2em;
  background: #fff;
  border-radius: 10.2em 0 0 10.2em;
  top: -0.1em;
  left: -0.1em;
  -webkit-transform-origin: 5.2em 5.1em;
  transform-origin: 5.2em 5.1em;
  -webkit-animation: load2 2s infinite ease 1.5s;
  animation: load2 2s infinite ease 1.5s;
}
.loader:after {
  width: 5.2em;
  height: 10.2em;
  background: #fff;
  border-radius: 0 10.2em 10.2em 0;
  top: -0.1em;
  left: 5.1em;
  -webkit-transform-origin: 0px 5.1em;
  transform-origin: 0px 5.1em;
  -webkit-animation: load2 2s infinite ease;
  animation: load2 2s infinite ease;
}
@-webkit-keyframes load2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.Modal {
    position: fixed;
    z-index: 500;
    width: 90%;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 2px;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
    padding: 16px;
    left: 50%;
    top: 20%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
    -webkit-transform: translate(-50%, -20%);
            transform: translate(-50%, -20%);
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50%-250px);
    }
}
